




































	import uniNavBar from '../../../components/uni-nav-bar.vue';
	export default {
		data() {
			return {
				radioItems: [{
						value: '1',
						name: '通道1'
					},
					{
						value: '2',
						name: '通道2'
					}
				],
				priceItem: ['20', '50', '100', '500', '1000', '2000'],
				current: 0,
				shows: 0,
				actualPrice: '20',
				payTitleList: [],
				payIdList: []
			};
		},
		onLoad() {
			this.$utils.tRequest({
				url: this.$webUrl + 'sys_payment_list',
				method: 'GET',
				data: {},
				success: res => {
					// console.log(res);
					if (res.code === 0) {
						var list = res.data.list;
						list.forEach(item => {
							this.payTitleList.push(item.title);
							this.payIdList.push(item.payment_id);
						});

					}
				},
				fail: () => {},
				complete: () => {}
			})
		},
		methods: {
			back() {
				uni.navigateBack({
					delta: 1
				})
			},
			radioChange: function(evt) {
				for (let i = 0; i < this.radioItems.length; i++) {
					if (this.radioItems[i].value === evt.target.value) {
						this.current = i;
						break;
					}
				}
			},
			changePrice: function(ind) {
				this.shows = ind;
				this.actualPrice = this.priceItem[ind];
			},
			onKeyInput: function(event) {
				this.shows = -1;
				// console.log(event.target.value);
				event.target.value === '' ? this.actualPrice = 0 : this.actualPrice = event.target.value.replace(/^[0]\d*$/, '');
			},
			payBtn: function() {
				if (this.actualPrice % 2 !== 0) {
					uni.showToast({
						title: '金额必须为2的倍数哦！',
						duration: 1500,
						icon: 'none'
					});
					return false;
				}
				if (this.actualPrice <= 0) {
					uni.showToast({
						title: '请检查输入金额是否有误！',
						duration: 1500,
						icon: 'none'
					});
					return false;
				}

				uni.showActionSheet({
					itemList: this.payTitleList,
					success: (e) => {
						// console.log(e.tapIndex);
						uni.showToast({
							title: "点击了第" + e.tapIndex + "个选项",
							icon: "none"
						})
						var pid = this.payIdList[e.tapIndex];
						this.payFun(pid);
					}
				});

			},
			payFun: function(pid) {
// 				console.log(pid);
// 				console.log(this.actualPrice);
				this.$utils.tRequest({
					url: this.$webUrl + 'user_amount_recharge',
					method: 'post',
					data: {
						order_amount: this.actualPrice,
						payment_id: pid
					},
					success: res => {
						console.log(res);
						if (res.code === 0) {
							this.$store.commit('setPayInfo', res.data);
							uni.navigateTo({
								url: './webPay',
								success: res => {}
							});
							
						}
					},
					fail: () => {},
					complete: () => {}
				})
			}

		},
		computed: {},
		components: {
			uniNavBar
		}
	}
